.container-toggle{
    position: absolute;
    display: flex;
    justify-content: space-around;
    top: 87%;
    right: 5%;
    width: 40%;
    align-items: center;
}
.line{
    border: 0.1px white solid;
    width: 45%;
}
.textToggle{
    font-size: 0.7vw;
    color: white;
    padding: 0 5%;
    letter-spacing: 0.1rem;
}
.arrowToggle{
    color: white;
    font-size: 1rem;
    cursor: pointer;
}
@media (max-width:768px){
    .container-toggle{
        top: 84%;
    }
}
@media (max-width:480px){
    .container-toggle{
        top: 80%;
    }
}
