.products-submenu {
    width: 100%;
    position: absolute;
    z-index: 1;
    left: 0;
    top: 100%;
}

.products-submenu li {
    background: rgb(0, 0, 0, 0.6);
    cursor: pointer;
}

.products-submenu.clicked {
    display: none;
}

.products-item {
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: white;
    padding: 2rem;
    font-size: large;
}

.products-item:hover {
    background-color: rgb(93, 93, 93, 0.8);
}